.wrapper {
    width: 460px;
    margin-left: 30px;
   /* background-color: #082103;*/
    /*border: 5px solid #B5E424;*/
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.affair {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #B5E424;
    /*color: #998FBA;*/
    padding: 2px;
    font-size: 18px;
}

.nameStyle {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 150px 0 10px;
    width: 100px;
    line-height: 12px;
}

.priorityStyle {
    margin: 0 auto;
    width: 100px;
}

.footer {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
}

@keyframes blink {
    0% {
        left: -130%;
    }
    100% {
        left: 130%;
    }
}

.filterButton {
    overflow: hidden;
    position: relative;
    width: 100px;
    background-color: cornflowerblue;
    opacity: 0.8;
    box-shadow: 0 0 5px 5px rgba(334, 360, 380, 0.2);
    /*background-color: #B5E424;*/
    padding: 0 2px;
    font-size: 18px;
    color: mediumvioletred/*#274021*/;
    border-radius: 4px;
}

