.filterButton::after {
    display: block;
    content: '';
    position: absolute;
    left: -130%;
    top: 0;
    height: 100%;
    width: 20%;
    transform: skew(45deg);
    background-color: #D8E3D4;
    opacity: 0.7;
    z-index: 14;
}

.filterButton:hover::after {
    animation: blink 1s ease;
}

.active {
    //background: #335533;
    background: #99ff99;
}

.deleteButton {
    display: flex;
    justify-content: center;
    width: 50px;
    background-color: cornflowerblue/*#B5E424*/;
    opacity: 0.8;
    padding: 0 10px;
    font-size: 15px;
    font-weight: bold;
    color: red/*#274021*/;
    box-shadow: 0 0 5px 5px rgba(334, 360, 380, 0.2);
    border-radius: 4px;
}

.hwtitle {
    color: teal;
    margin-left: 30px;
    text-shadow: 4px 4px 10px red, 0 0 2em blue, 0 0 0.5em blue;
    /*text-shadow: #FC0 1px 0 10px;*/
}

.high {
    color: red;
}

.middle {
    color: orange;
}

.low {
    color: lime;
}